.container {
  @media (min-width: 1200px) {
    max-width: 1440px !important;
  }
}

#filter {
  display: flex;
  flex-direction: column;
  gap: 20px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;

  .couseCertificatesByTopic {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .couseCertificatesByTopicItem {
      display: flex;
      align-items: center;
      gap: 10px;

      .checkbox {
        padding: 0px;
        transform: scale(1.2);
      }

      .couseCertificatesByTopicTitle {
        span {
          color: var(--gray-60);
        }
      }
    }
  }
}

#banner {
  height: 300px;
  display: flex;
  align-items: center;

  #bannerContainer {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  #bannerSearch {
    display: flex;
    gap: 15px;
    align-items: center;
  }
}

#couseCertificatesWrapper {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .couseCertificatesByTopic {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
}
