.imageLogin {
  width: 100%;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 40px;
}
.loginContainer {
  background-color: white !important;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  width: 100%;
  padding: 40px 20px;
  border-radius: 8px;

  .formControl {
    display: flex;
    flex-direction: column;
    width: 80%;
    gap: 10px;
  }
  .social {
    display: flex;
    justify-content: center;
    gap: 10px;
    .socialIcon {
      padding: 10px;
      color: white;
      border-radius: 8px;
    }
    .socialIconGoogle {
      background-color: #db4437;
    }
    .socialIconMicrosoft {
      background-color: #00a4ef;
    }
  }
}
